.Menu{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    .m30{
        width: 30%;
    }
    .m40{
        width: 40%;
    }
    .menuList{
        font-family: 'Noto Sans KR', sans-serif;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        li{
            cursor: pointer;
            width: 25%;
            font-weight: 700;
            font-size: 2.4rem;
            padding: 2rem;
            text-align: center;
            transition: .4s ease-in-out;
            a{color: inherit;}
            span{position: relative;}
        }
        li:hover{
            color: #6588e6;
            span::before{
                content: "";
                width: 100%;
                position: absolute;
                bottom: -2px;
                height: 3px;
                left: 0;
                transform: translate3d(0,5px,0);
                transition: all .7s cubic-bezier(.175,.885,.32,1.275);
                background: #6588e6;
            }
        }
    }
    .contactList{
        display: flex;
        flex-direction: row;
        justify-content: flex-end;
        li{
            font-size: 2.4rem;
            padding: 20px;
            a{
                padding-right: 40px;
            }
        }
    }
}

@media screen and (max-width: 1520px) {
    .Menu{
        .m40{width:0%}
        .m30.menuArea{
            width: 70%;
        }
    }    
}

@media screen and (max-width: 820px) {
    .blankArea, .logoArea{display: none;}
    .Menu{
        .m30.menuArea{
            width: 100%;
            ul{
                li{padding: 1rem;font-size: 2rem;}
            }
        }
    }    
}