.Work{
    width: 100%;
    min-height: 100vh;
    font-size: 1.6rem;
    .TimeLineArea{
        max-width: 740px;
        margin: 0 auto;
        .timeline-item{
            display: flex;
            justify-content: space-between;
            align-items: stretch;
            box-sizing: border-box;
            margin: 14rem 0;
            padding: 2rem;
            .timeline{
                position: relative;
                box-sizing: border-box;
                .timeline-icon{
                    position: relative;
                    width: 40px;
                    height: 40px;
                    padding: 2px;
                    border-radius: 10px;
                    background-color: #fff;
                    box-shadow : 0 4px 6px -1px rgba(0,0,0,0.1),0 2px 4px -1px rgba(0,0,0,0.06);
                    z-index: 9;
                }
            }
            .timeline::before{
                content: "";
                width: 2px;
                height: 100%;
                z-index: 1;
                position: absolute;
                left: 50%;
                top: 0;
                transform: translate3d(-50%,0,0);
                background: #6588e6;
            }

            .timeline-content{
                h1{
                    padding-left: 1rem;
                    font-size: 3rem;
                    font-family: 'Gowun Batang', serif;
                }
                .date{
                    font-family: 'Jua', sans-serif;
                    font-size: 2.2rem;
                    color: #999999;
                }
                a{
                    font-size: 2rem;
                    font-weight: 700;
                    color: #000;
                    border-bottom: 2px solid #6588e6;
                }
                p{
                    margin: 10px 0;
                    color: #656270;
                }
                small{
                    font-size: 1.4rem;
                }
                .img-area{
                    width: 100%;
                    margin: 8rem 0;
                    img{
                        width: 100%;
                        border-radius: 20px;
                        box-shadow: 0 52px 74px rgb(0 21 64 / 14%);
                    }
                }
            }
        }
        .timeline-item:first-child{
            margin: 0 0 5rem 0;
        }
    }
}