.About{
    clear: both;
    width: 100%;
    display: flex;
    flex-direction: column;
    .about-content{
        width: calc(100% - 28rem);
        padding: 5rem 14rem;
        margin: 8rem auto;
        h1{
            font-family: 'Gowun Batang', serif;
            font-size: 3rem;
            padding: 0 0 20px 0;
        }
        p{
            font-size: 2rem;
            padding: 20px 0;
            color: #777
        }
    }

    .introduce{
        .introduce-area{
            display: flex;
            flex-direction: row;
            .introduce-left, .introduce-right{ width: 50% }
            .introduce-left{
                padding: 5%;
                text-align: center;
                img{
                    width: 90%;
                    border-radius: 10px;
                }
            }
            .introduce-right{
                h2{
                    font-family: 'Gowun Batang', serif;
                    font-size: 2.8rem;
                    line-height: 5rem;
                }
                p{
                    font-size: 1.8rem;
                    line-height: 5rem;
                }
            }
        }
    }

    .career{
        .career-line{
            margin: 8rem 0;
            width: 100%;
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            .career-item{
                width: calc(25% - 10rem);
                color: #fff;
                background-color: #6588e6;
                font-size: 2rem;
                text-align: left;
                position: relative;
                div{
                    padding: 1.5rem;
                }
            }
            .career-item::before{
                content: "";
                position: absolute;
                left: -5.9rem;
                top: 0;
                width: 0;
                height: 0;
                border-right: 5.9rem solid #6588e6;
                border-top: 5.9rem solid transparent;
                border-bottom: 5.9rem solid transparent;
                
            }
            .career-item::after{
                content: "";
                position: absolute;
                right: 0;
                bottom: 0;
                width: 0;
                height: 0;
                border-right: 5.9rem solid white;
                border-top: 5.9rem solid transparent;
                border-bottom: 5.9rem solid transparent;
            }
        }
    }

    .activity{
        display: flex;
        flex-direction: column;
        .activity-area{
            margin: 0 0 6rem 0;
            .activity-title{
                margin: 3rem 0;
                font-size: 3rem;
                text-align: center;
                font-family: 'Gowun Batang', serif;
                font-weight: 700;
            }
            .activity-content{
                p{
                    a{font-size: 1.6rem;color: #aaa;transition: .5s all ease-in-out;}
                    a:hover{color: #000}
                }
                h3{font-size: 2rem; font-weight: 700; padding: 4rem 0 0 0;}
                .favorite-list{
                    display: flex;
                    flex-direction: row;
                    justify-content: space-between;
                    div{
                        cursor: pointer;
                        text-align: center;
                        width: 25%;
                        transition: .5s all ease-in-out;
                        .img{
                            width: calc(100% - 4rem);
                            padding: 2rem;
                            img{
                                width: 100%;
                            }
                        }
                        span{
                            font-size: 1.4rem;
                            color: #000;
                            transition: .5s all ease-in-out;
                        }
                    }
                    div:hover{
                        opacity: 0.8;
                        span{
                            color: #3764e0
                        }
                    }
                }
            }
        }
    }
    .txt-rg{text-align: right;}
}


@media screen and (max-width: 1380px) {
    .About{
        .career{
            .career-line{
                .career-item{
                    width: calc(25% - 2rem);
                    margin: 0;
                    font-size: 1.6rem;
                    div{padding: 0.3rem;}
                }
                .career-item::before{
                    content: "";
                    left: -3rem;
                    border-right: 3rem solid #6588e6;
                    border-top: 3rem solid transparent;
                    border-bottom: 3rem solid transparent;
                    
                }
                .career-item::after{
                    content: "";
                    border-right: 3rem solid white;
                    border-top: 3rem solid transparent;
                    border-bottom: 3rem solid transparent;
                }
            }
        }
    }
}


@media screen and (max-width: 1070px) {
    .About{
        .about-content{
            width: calc(100% - 6rem);
            padding: 1rem 3rem;
            h1{
                font-size: 3rem;
                padding: 0 0 20px 0;
            }
            p{
                font-size: 1.2rem;
                padding: 10px 0;
            }
        }
        .introduce{
            .introduce-area{
                flex-direction: column;
                .introduce-left{
                    padding: 0 0 10px 0;
                    width: 100%;
                    img{width: 100%;}
                }
                .introduce-right{ width: 100% }
                .introduce-right{
                    h2{
                        font-family: 'Gowun Batang', serif;
                        font-size: 2.2rem;
                        line-height: 3rem;
                    }
                    p{
                        font-size: 1.6rem;
                        line-height: 3rem;
                    }
                }
            }
        }
    }
}

@media screen and (max-width: 740px) {
    .About{
        .career{
            .career-line{
                margin: 0;
                width: 100%;
                display: flex;
                flex-direction: column;                
                .career-item{
                    width: 50%;
                    margin: 2rem auto;
                    font-size: 1.6rem;
                    div{
                        padding: 1rem;
                    }
                }
                .career-item::before{
                    content: "";
                    left: -4.4rem;
                    border-right: 4.4rem solid #6588e6;
                    border-top: 4.4rem solid transparent;
                    border-bottom: 4.4rem solid transparent;
                    
                }
                .career-item::after{
                    content: "";
                    border-right: 4.4rem solid white;
                    border-top: 4.4rem solid transparent;
                    border-bottom: 4.4rem solid transparent;
                }
            }
        }

        .activity{
            .activity-area{
                .activity-content{                    
                    .favorite-list{
                        flex-direction: row;
                        flex-wrap: wrap;
                        div{ width: 50%; }
                    }
                }
            }
        }
    }
}