.Main{
    background-color: #fff;
    color:#000;
    min-height: 100vh;
    display: flex;
    position: relative;
    .introduce_area{
        position: absolute;
        top: 0;
        left: 0;
        padding: 5rem 10rem;
        width: calc(100% - 20rem);
        height: 100%;
        font-family: 'Gowun Batang', serif;
        h1{font-size: 6rem;}
        p{font-size: 2rem; padding: 20px 0; color:#777;}
    }
    #myCanvas{
        // width: 100%;
        // height: 100%;
        position: absolute;
        top: 0;
        left: 0;
    }
}