.ChatBot{
    clear: both;
    color: #000;
    font-size: 1.6rem;
    .hideMode{
        position: fixed;
        right: 80px;
        bottom: 80px;
        background-color: #6588e6;
        box-shadow : 0 4px 6px -1px rgba(0,0,0,0.1),0 2px 4px -1px rgba(0,0,0,0.06);
        color: #fff;
        border-radius: 50%;
        padding: 10px;
        text-align: center;
        width: 32px;
        height: 32px;
    }
    .chatMode{
        position: fixed;
        right: 30px;
        bottom: 40px;
        width: 400px;
        height: 600px;
        .chatBox{
            width: 100%;
            height: 100%;
            background-color: #fff;
            box-shadow : 0 4px 6px -1px rgba(0,0,0,0.1),0 2px 4px -1px rgba(0,0,0,0.06);
            border-radius: 20px;
            display: flex;
            flex-direction: column;
            .chatHeader{
                border-radius: 20px 20px 0 0;
                background-color: #6588e6;
                display: flex;
                flex-direction: row;
                justify-content: space-between;
                color: #fff;
                padding: 20px;
                .myProfile{
                    padding: 0 0 0 15px;
                    img{
                        border-radius: 50%;
                        width: 48px;
                        height: 48px;
                    }
                }
                .chatTitle{
                    text-align: center;
                    .title{font-weight: 900;}
                }
                .chatClose{
                    cursor: pointer;
                    padding: 8px 0 0 0;
                }
            }
            .chatContent{
                width: 100%;
                height: auto;
                overflow: auto;
                display: flex;
                flex-direction: column;
                font-size: 1.4rem;                
                .chatRow{
                    width: 100%;
                    .msg{
                        padding: 10px 15px;
                        margin: 10px;
                        border-radius: 15px;
                        color: #000;
                        a{color: rgb(43, 0, 201);}
                    }
                    .query{
                        cursor: pointer;
                        background-color: rgb(225, 227, 255);
                        float: left;
                    }
                    .query:hover{
                        background-color: rgb(200, 204, 253);
                    }
                    .answer{
                        border: 1px solid #ddd;
                        float: left;
                    }
                    .question{
                        float: right;
                        color: #fff;
                        background-color: #000;
                    }
                }
            }
        }
    }
}

@media screen and (max-width: 470px) {
    .ChatBot{
        .hideMode{
            right: 40px;
            bottom: 40px;
        }
        .chatMode{
            top: 0;
            left: 0;
            width: 100%;
            height: 100vh;
        }
    }
}