@import url('https://fonts.googleapis.com/css2?family=Gowun+Batang&family=Jua&family=Noto+Sans+KR&display=swap');


*{margin: 0; padding: 0;}
html, body{width: 100%; height: 100vh; font-size: 10px; font-family: 'Noto Sans KR', sans-serif;}
body{background-color: #fff; color: #000;}
ul, li, ol{list-style: none;}
a{text-decoration: none;}
a:link, a:active, a:visited, a:hover{color: #000;}