.Footer{
    width: 100%;
    background-color: #6588e6;
    color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 50px 0;
    ul{
        li{            
            text-align: center;
            font-size: 1.8rem;
            padding: 10px 0;
        }
        li:last-child{
            a{
                padding: 20px;
            }
        }
    }
    hr{width: 90%; margin: 20px 0; color:#fff;}
}